<!-- by 添少 -->
<template>
  <section class="article">
    <section class="article-title">
      <h3>
        <slot name="title"></slot>
      </h3>
    </section>
    <section class="article-summary">
      <p>
        <slot name="summary"></slot>
      </p>
    </section>
    <section class="article-content">
      <slot></slot>
    </section>
  </section>
</template>

<script>
  export default {
    name: 'ui-article'
  }
</script>

<style lang="scss" scoped>
  .article {
  }

  .article-title {
    padding: 5px 0;
    border-bottom: dashed #d7d7d7 1px;
    h3 {
      font-size: 24px;
    }
  }

  .article-summary {
    font-size: 16px;
    color: #555;
    padding: 15px 0;
    border-bottom: dashed #d7d7d7 1px;
  }

  .article-content {
    padding: 10px 0;
  }
</style>



