<!-- by 添少 -->
<template>
  <fixed-layout>
    <ui-article>
      <template slot="title">{{title||'标题'}}</template>
      <template slot="desc">{{desc||'简介'}}</template>
      <section v-html="content||'内容'"></section>
    </ui-article>
    <template slot="footer">
      <section class="bar">
        <button class="primary" style="flex-basis:30%;" @click="handSubmit">发布行程</button>
        <button @click="handSave">存草稿</button>
      </section>
    </template>
  </fixed-layout>
</template>
<script>
  import { mapState } from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import UiArticle from '@/components/ui-article'

  export default {
    name: '',
    components: {
      FixedLayout,
      UiArticle
    },
    props: {},
    data () {
      return {}
    },
    watch: {},
    computed: {
      ...mapState({
        cover: state => state.write.article.cover,
        clubId: state => state.write.article.clubId,
        title: state => state.write.article.title,
        desc: state => state.write.article.desc,
        content: state => state.write.article.content,
      })
    },
    created () {
      this.init()
    },
    mounted () {
    },
    methods: {
      init () {
      },
      initData () {},
      saveArticle (type) {
        let params = {
          productId: 0,
          clubId: this.clubId,
          contType: 0,
          title: this.title,
          desc: this.desc,
          preimg: this.cover,
          cont: this.content,
          contStatus: type
        }
        this.$http.post(this.$port.ADD_PRODUCT, params).then(res => {
          alert(res.msg)
        })
      },
      handSubmit () {
        this.saveArticle(2)
      },
      handSave () {
        this.saveArticle(1)
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .fixed-layout {
    background-color: #fff;
  }

  .article {
    padding: 0 15px;
  }

  .bar {
    background-color: #fff;
    border-top: solid #1797c6 1px;
    display: flex;
    button {
      background-color: #fff;
      font-size: 18px;
      color: #38a6ce;
      padding: 10px 0;
      border: none;
      flex: 1;
      &.primary {
        background: linear-gradient(to bottom, #4cafd3, #1797c6);
        color: #fff;
      }
    }
  }

</style>



